body{
background-color:#7460a7;

}

.App{
  background-color: #3f3160;
  min-height: 500px;
  width:400px;
  margin:25px auto;
}

#to-do-form input{
  background-color: rgb(240, 228, 237);
  border:0;
  width:200px;
  height:50px;
  padding:0 20px;
  margin:20px;
  border-radius: 9px;
  font-size: 18px;
  font-weight: 300px;
  color: rgb(34, 29, 29);
}

#to-do-form input ::placeholder{
  color:rgba(255,255,255,0.5);
}

#to-do-form input:focus,#to-do-form button:focus{
  outline: none;
}

#to-do-form button{
  height:50px;
  width: 60px;
  border-radius: 6px;
  border:0;
   cursor: pointer;
   background-color: rgb(245, 157, 42);
   font-weight: 600;
   letter-spacing: 2px;
   color: cornsilk;
}

#to-do-form button:hover{
color: rgb(41, 36, 23);
}