.list{
    color:#fff;
    width: 300px;
    background-color: #bf0744;
    height:50px;
    margin: 20px auto;
    border-radius: 5px;
}

.list p{
    padding: 10px;
    position: relative;
}

.list p input{
    border:0;
    color: #fff;
    background-color: transparent;
    font-size: 20px;

}
.list p span{
    position: absolute;
    right:10px;
    cursor: pointer;
    
}

.faicons{
    margin: 0 4px;
    font-size: 14px;
}